html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  color: hsla(0, 0%, 0%, 0.8);
  font-weight: normal;
  font-size: 100%;
  word-wrap: break-word;
  font-kerning: normal;
  -moz-font-feature-settings: "kern", "liga", "clig", "calt";
  -ms-font-feature-settings: "kern", "liga", "clig", "calt";
  -webkit-font-feature-settings: "kern", "liga", "clig", "calt";
  font-feature-settings: "kern", "liga", "clig", "calt";
  min-height: 100%;
  line-height: 1.4;
  font-family: Segoe UI, -apple-system, BlinkMacSystemFont, Roboto, Oxygen,
  Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

html {
  box-sizing: border-box;
  overflow-y: scroll;
}

* {
  box-sizing: inherit;
}

h1 {
  font-size: 1.5rem;
  margin: .67rem 0;
}

h3 {
  font-size: 1.2rem;
  margin: auto;
}

header {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-between;
  background-color: #363640;
  box-shadow: 0 3px 12px rgba(17, 17, 17, 0.8);
  color: #fff;
  padding: 0.5rem;
}

h6 {
  font-size: 1.25rem;
  font-weight: 400;
  margin: 3rem 0rem 3rem 3rem;
}

main {
  display: flex;
  width: 100%;
  padding: 1rem;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

section {
  max-width: 400px;
  margin: 1rem 0;
  align-self: center;
}

label {
  display: block;
}

label input {
  display: block;
}

button {
  height: 2rem;
  min-width: 6rem;
  background-color: white;
  border: 2px solid #363640;
}

button.disabled, button:disabled {
  border: 2px solid #5e5e66;
  background-color: #fafafa;
}

nav {
  position: relative;
  width: 100%;
  padding: 0 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: center;
}

nav button {
  color: white;
  background: none;
}

nav button.active {
  border-bottom: 2px solid white;
  background-color: inherit;
}

.segment {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr;
}

.snackbar {
  position: fixed;
  bottom: 10%;
  left: 50%;
  visibility: visible;
  transform: translateX(-50%);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: center;
  margin: 0 auto;
  padding-left: 1rem;
  padding-right: 1rem;
  height: 3rem;
  color: white;
  background-color: #2f2f2f;
  border-radius: 3px;
  max-width: 80%;
  min-width: 300px;
  box-shadow: 0 3px 12px rgba(17, 17, 17, 0.8);
  opacity: 1;
  transition: visibility 300ms ease-in-out, opacity 300ms ease-in-out;
}

.snackbar__hidden {
  visibility: hidden;
  opacity: 0;
}

.snackbar p {
  margin: auto 0;
}

.layer-4 {
  box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.1);
}

.layer-5 {
  box-shadow: 0 3px 12px rgba(17, 17, 17, 0.8);
}
